.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.container{
  margin-left:0px;
}
.header-style{
  font-weight: bold;
}
.break{
  text-align: center; 
  height: 40px;
  color: rgb(245, 126, 14);
  background-color: #f5f5be;
  font-weight: bold;
  font-size: 30px;
  
  border: 3px solid  rgb(245, 126, 14);

}
.objective{
  text-align: left;
}
.ul{
  list-style-type:disc;
}
.justify{
 
  text-align: left;
 
  font-size: 19px;
  font-family: "roboto";
}
.list-style{
  list-style-type: none;
}
div{
  font-family: 'Roboto';
  font-size: 25px;
}
.head{
  font-size: 50px;
  text-decoration: underline;
}
.hr-line{
  background-color: rgb(245, 126, 14) ;
}